import React from 'react';
import './Order.css'
import {Descriptions} from "antd";
import {getTotalPrice, imageEndpoint} from "../../services/GoodService";
import Paragraph from "antd/es/typography/Paragraph";
import { Space, Typography } from 'antd';
const { Text, Link } = Typography;

const Order = ({client, goodsWithAmount, message, methodOfDelivery, address, goodId}) => {

    return (
        <div className={"order"}>
            {goodsWithAmount.map(goodWithAmount => (
                <div className={"goodWithAmount"} key={goodWithAmount.good.id}>
                    <div>
                        <img src={imageEndpoint + goodWithAmount.good.imageId} alt=""/>
                        <span>{goodWithAmount.good.name}</span>
                    </div>
                    <div>
                        <span>{goodWithAmount.amount} .</span>
                        <span>: {goodWithAmount.good.price} .</span>
                    </div>
                    <hr/>
                </div>
            ))}

            <Descriptions title=" ">
                <Descriptions.Item label="'">{client.name}</Descriptions.Item>
                <Descriptions.Item label="">
                    <Text style={{color: "var(--tg-theme-text-color)"}} copyable={{
                        tooltips: [' ', '!']
                    }}>{client.phoneNumber}</Text>
                </Descriptions.Item>
                <Descriptions.Item label=" ">{methodOfDelivery}</Descriptions.Item>
                {methodOfDelivery === "'"
                    ?
                    <Descriptions.Item label="">{address}</Descriptions.Item>
                    :
                    <div></div>
                }
                <Descriptions.Item label=" ">{getTotalPrice(goodsWithAmount)}</Descriptions.Item>

                {message
                ?
                    <Descriptions.Item label="">{message}</Descriptions.Item>
                :
                    <div></div>
                }
                <Descriptions.Item label="ID">{goodId}</Descriptions.Item>
            </Descriptions>
        </div>
    );
};

export default Order;